          "
        />
        <span
          style="
            color: #322E2B;
            font-size: 25px;
            font-weight: 900;
            position: absolute;
            left: 89px;
            cursor: pointer;
          "
          >地质信息数字化平台</span
        >
      </div>
      <!-- <div style="flex:1;background-color:skyblue;height: 5rem;"></div> -->
      <div class="quickSelect" style="flex: 2; height: 5rem">
        <el-row>
          <el-col
            :span="6"
            style="
              display: flex;
              justify-content: center;
              align-items: center;
              height: 5rem;
            "
            ><div style="width: 100%; height: 3.125rem">
              <div>
                <i
                  style="cursor: pointer; color: #2e6dfc; font-size: 2.1875rem"
                  class="el-icon-star-off"
                ></i>
              </div>
              <div
                style="
                  cursor: pointer;
                  font-size: 0.875rem;
                  font-weight: 500;
                  color: #2e6dfc;
                "
              >
                我的收藏
              </div>
            </div></el-col
          >
          <el-col
            :span="6"
            style="
              display: flex;
              justify-content: center;
              align-items: center;
              height: 5rem;
            "
            ><div style="width: 100%; height: 3.125rem">
              <div>
                <i
                  style="cursor: pointer; color: #2e6dfc; font-size: 2.1875rem"
                  class="el-icon-s-data"
                ></i>
              </div>
              <div
                style="
                  cursor: pointer;
                  font-size: 0.875rem;
                  font-weight: 500;
                  color: #2e6dfc;
                "
              >
                我的数据
              </div>
            </div></el-col
          >
          <el-col
            :span="6"
            style="
              display: flex;
              justify-content: center;
              align-items: center;
              height: 5rem;
            "
            ><div style="width: 100%; height: 3.125rem" @click="quickUpload">
              <div>
                <i
                  style="cursor: pointer; color: #2e6dfc; font-size: 2.1875rem"
                  class="el-icon-folder-add"
                ></i>
              </div>
              <div
                style="
                  cursor: pointer;
                  font-size: 0.875rem;
                  font-weight: 500;
                  color: #2e6dfc;
                "
              >
                我要上传
              </div>
            </div></el-col
          >
          <el-col
            :span="6"
            style="
              display: flex;
              justify-content: center;
              align-items: center;
              height: 5rem;
            "
            ><div style="width: 100%; height: 3.125rem">
              <div>
                <i
                  style="cursor: pointer; color: #2e6dfc; font-size: 2.1875rem"
                  class="el-icon-search"
                ></i>
              </div>
              <div
                style="
                  cursor: pointer;
                  font-size: 0.875rem;
                  font-weight: 500;
                  color: #2e6dfc;
                "
              >
                我要查询
              </div>
            </div></el-col
          >
          
        </el-row>
      </div>
      <div v-if="!hasToken" class="logBtn" style="flex: 1; height: 5rem">
        <el-row>
          <el-col
            :span="12"
            style="
              display: flex;
              justify-content: center;
              align-items: center;
              height: 5rem;
              color: #2e6dfc;
              cursor: pointer;
            "
            ><router-link to="/register" style="color: #2e6dfc"
              >注册</router-link
            ></el-col
          >
          <el-col
            :span="12"
            style="
              display: flex;
              justify-content: center;
              align-items: center;
              height: 5rem;
            "
          >
            <button
              style="
                background-color: #2e6dfc;
                color: #fff;
                height: 2.1875rem;
                width: 80%;
                text-align: center;
                cursor: pointer;
                border: none;
              "
            >
              <router-link to="/" style="color: #fff"
                ><div style="width: 100%; height: 100%; line-height: 2.1875rem">
                  登录
                </div></router-link
              >
            </button>
          </el-col>
        </el-row>
      </div>
      <div
        v-if="hasToken"
        class="avatar"
        style="
          flex: 0.5;
          height: 5rem;
          display: flex;
          justify-content: center;
          align-items: center;
        "
      >
        <el-dropdown trigger="click">
          <el-avatar style="cursor: pointer" :src="circleUrl"></el-avatar>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item
              ><span @click="centerDialogVisible = true"
                >退出登录</span
              ></el-dropdown-item
            >
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <el-dialog
        :modal-append-to-body="false"
        :visible.sync="centerDialogVisible"
        width="30%"
        center
      >
        <div
          style="height: 20px; width: 100%; text-align: center; font-size: 20px"
        >
          确定要退出吗？
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="centerDialogVisible = false">取 消</el-button>
          <el-button
            type="primary"
            @click="
              centerDialogVisible = false;
              logout();
            "
            >确 定</el-button
          >
        </span>
      </el-dialog>
    </div>
    <div class="mynav">
      <el-menu
        :default-active="activeIndex"
        class="el-menu-demo"
        mode="horizontal"
        @select="handleSelect"
      >
        <el-menu-item index="1">首页</el-menu-item>
        <el-submenu index="2">
          <template slot="title">数据字典管理</template>
          <el-menu-item index="2-1"
            ><router-link to="/fieldMap" style="color: #000"
              >数据模型管理</router-link
            ></el-menu-item
          >
          <el-menu-item index="2-2"
            ><router-link to="/sysMdlControl" style="color: #000"
              >业务字典配置</router-link
            ></el-menu-item
          >
          <el-menu-item index="2-3"
            ><router-link to="/serverMdlControl" style="color: #000"
              >业务字典管理</router-link
            ></el-menu-item
          >
          <el-submenu index="2-4">
            <template slot="title">选项4</template>
            <el-menu-item index="2-4-1">选项1</el-menu-item>
            <el-menu-item index="2-4-2">选项2</el-menu-item>
            <el-menu-item index="2-4-3">选项3</el-menu-item>
          </el-submenu>
        </el-submenu>
        <el-menu-item index="3">我要上传</el-menu-item>
        <el-menu-item index="4">成果管理</el-menu-item>
        <el-menu-item index="5">
          <a @click="handleAuthInfo" :href="getMarketUrl">数据集市</a>
        </el-menu-item>
      </el-menu>
    </div>
  </el-header>
</template>

<script>
import { eventBus } from '@/main.js';
import { Base64 } from 'js-base64';
export default {
  data() {
    return {
      activeIndex: "1",
      //默认头像
      circleUrl: require("../../assets/images/logo.png"),
      hasToken: false, //判断token存在来控制头像显示
      token: localStorage.getItem("tyytrade-web_TOKEN"), //获取token
      Authorization:localStorage.getItem("tyytrade-web_USER"), //获取身份
      centerDialogVisible: false, //退出登录弹框
      handleUrl:'',
    };
  },
  watch: {
    'token': {
      immediate: true,
      handler(newToken) {
        console.log("Token 发生变化，执行逻辑");
        if (newToken) {
          console.log("执行true")
            this.hasToken = true;
        } else {
          console.log("执行false")
            this.hasToken = false;
        }
      },
    },
  },

  created() {
    eventBus.$on('checkAvatar', this.checkState);
  },
  computed: {
    getMarketUrl() {
      return `http://47.101.190.235:9898/#/home?token=${this.handleUrl}`;
    },
  },
  methods: {
    handleAuthInfo(){
      let token = localStorage.getItem("tyytrade-web_TOKEN");
      token = token.replace(/"/g, '');
      this.handleUrl = token;
    },
    //快捷进入
    quickUpload() {
      this.$router.push("/dataResult");
    },
    //导航栏切换
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
      switch (key) {
        case "1":
          this.$router.push("/home");
          break;
        case "2-1":
          this.$router.push("/fieldMap");
          break;
        case "2-2":
          this.$router.push("/sysMdlControl");
          break;
        case "2-3":
          this.$router.push("/serverMdlControl");
          break;
        case "3":
          this.$router.push("/dataResult");
          break;
        case "4":
          this.$router.push("/catalog");
          break;
        default:
          return;
      }
    },
    //退出登录 清除token
    logout() {
      localStorage.removeItem("tyytrade-web_TOKEN");
      // this.token = '';  //清除token里面的缓存
      this.checkState();
      this.$message({
        type:'info',
        message:'已退出'
      })
      this.$router.push("/");
    },
    checkState(){
      const token = localStorage.getItem('tyytrade-web_TOKEN');
      // console.log("tyytrade-web_TOKEN",token);
      if(token){
        this.hasToken = true;
      }else {
        this.hasToken = false;
      }
    },
  },
  mounted() {
  },
};
</script>
<style scoped>
/* s */
/* 穿透控制导航栏的样式 */
:deep(.el-menu--horizontal > .el-menu-item) {
  height: 2.5rem;
  line-height: 2.5rem;
}
/* .mynav .el-menu--horizontal>.el-submenu .el-submenu__title {
     height: 2.5rem!important;
  line-height: 2.5rem!important;
} */
:deep(.el-menu--horizontal > .el-submenu .el-submenu__title) {
  height: 2.5rem !important;
  line-height: 2.5rem !important;
}
.el-header {
  background-color: #fff;
  color: #333;
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  /* //120px */
  height: 7.5rem !important;
  /* line-height: 6.25rem; */
  z-index: 999;
}
/* e */
</style>
